// foreignObject
export const createForeignObjectNode = ({ el, width, height }) => {
  const foreignObject = new ForeignObject()
  if (width !== undefined) {
    foreignObject.width(width)
  }
  if (height !== undefined) {
    foreignObject.height(height)
  }
  foreignObject.add(el)
  return foreignObject
}

