export const addXmlns = el => {
  el.setAttribute('xmlns', 'http://www.w3.org/1999/xhtml')
}

// sortIndex
export const sortNodeList = nodeList => {
  nodeList = [...nodeList]
  nodeList.sort((a, b) => {
    return a.sortIndex - b.sortIndex
  })
  return nodeList
}

