import { nextTick } from '.'

//  
class BatchExecution {
  //  
  constructor() {
    this.has = {}
    this.queue = []
    this.nextTick = nextTick(this.flush, this)
  }

  //  
  push(name, fn) {
    if (this.has[name]) {
      this.replaceTask(name, fn)
      return
    }
    this.has[name] = true
    this.queue.push({
      name,
      fn
    })
    this.nextTick()
  }

  // 
  replaceTask(name, fn) {
    const index = this.queue.findIndex(item => {
      return item.name === name
    })
    if (index !== -1) {
      this.queue[index] = {
        name,
        fn
      }
    }
  }

  //   
  flush() {
    let fns = this.queue.slice(0)
    this.queue = []
    fns.forEach(({ name, fn }) => {
      this.has[name] = false
      fn()
    })
  }
}

export default BatchExecution
