// LRU
export default class Lru {
  constructor(max) {
    this.max = max || 1000
    this.size = 0
    this.pool = new Map()
  }

  add(key, value) {
    const isExist = this.has(key)
    // key
    if (!isExist && this.size >= this.max) {
      return false
    }
    // 
    // key
    this.delete(key)
    // 
    this.pool.set(key, value)
    this.size++
    // https://github.com/wanglin2/mind-map/issues/467
    // if (this.size > this.max) {
    //   let keys = this.pool.keys()
    //   let last = keys.next()
    //   this.delete(last.value)
    // }
    return true
  }

  delete(key) {
    if (this.pool.has(key)) {
      this.pool.delete(key)
      this.size--
    }
  }

  has(key) {
    return this.pool.has(key)
  }

  get(key) {
    if (this.pool.has(key)) {
      return this.pool.get(key)
    }
  }

  clear() {
    this.size = 0
    this.pool = new Map()
  }
}
